import "./Home.css";
import RecipeReviewCard from "../../Product/UseCard/UseCard";
import axios from "axios";
import config from "../../../Configs/Configs";
import { Component } from "react";
import productModel from "../../../Modules/productModel";
import setHeaders from "../../../Services/setHeaders";
import Search from "../../ShareArea/Search/Search";
import { Grid, Link, Typography } from "@mui/material";
import AddImg from '../../../Assets/Images/TAG-6.jpg'
import RingsLinkImg from '../../../Assets/Images/ER540.jpg'
import EaringLinkImg from '../../../Assets/Images/R.png'


interface TahshitimModelState {
    products:productModel[];
    product1:productModel
    }
    class Home extends Component<{}, TahshitimModelState> {
        public constructor(props: {}) {
            super(props);
            this.state = {
                products:[],
                product1:null
            };
        }
        
        public async componentDidMount(){
            const headers  = setHeaders();
            try {
                const res = await axios.get<productModel[]>(config.urls.product + "sales",{headers:headers})
                this.setState({products:res.data});
            } catch (error) {
                alert(error);
            }
           
        }
    
        public render(): JSX.Element {
            
        return (
            <div className="Home">
                <Grid item xs={12} sx={{ border: '2px solid whitesmoke' ,marginTop:'30px'}}>
                    <Link href='/clocks'>
                        <img src={AddImg} width='100%' />
                    </Link>
                </Grid>
                <Grid container>

                    <Grid item xs={12} sm={6} sx={{textAlign:'center',display :'flex' ,border:'2px solid whitesmoke', marginTop :'30px',justifyContent:'center'}}>
                        <Typography sx={{ textAlign: 'center', fontFamily: 'Arial', fontSize: '20px' }}>
                            טבעות
                        </Typography>
                        <Link href='/rings'>
                       < img src={RingsLinkImg} height='200px' />
                        </Link>
                        
                    </Grid>
                    <Grid item xs={12} sm={6} sx={{ textAlign:'center',  display :'flex' ,marginTop :'30px',border:'2px solid whitesmoke',justifyContent:'center' }}>
                        <Typography sx={{ textAlign: 'center', fontFamily: 'Arial', fontSize: '20px' }}>
                            עגילים
                        </Typography>
                        <Link href='/earings'>
                        <img src={EaringLinkImg} height='200px' />
                        </Link>
                        
                    </Grid>
                    
                        <Grid item xs ={12} sx={{border:'2px solid whitesmoke' ,height:'150px', marginTop:'30px' ,textAlign:'center'}}>
                        <Typography sx={{fontFamily:'Arial' ,fontSize:'40px'}} >
                            מבצעים מיוחדים של השבוע 
                        </Typography>
                        </Grid>
                </Grid>
                <Grid sx={{ marginTop: '30px' }}>
                    {this.state.products?.map(p => <RecipeReviewCard key={p.id} product={p} />)}
                </Grid>


            </div>
        );
    }
}

export default Home;